@import 'swiper/scss';
@import 'swiper/scss/pagination';
@import 'swiper/scss/navigation';

:root {
  --swiper-theme-color: $gray-900;
  --swiper-pagination-bottom: 0.25rem;
  --swiper-pagination-color: #{$gray-900};
  --swiper-pagination-bullet-width: 0.75rem;
  --swiper-pagination-bullet-height: 0.5rem;
  --swiper-pagination-bullet-border-radius: 0.5rem;
  --swiper-navigation-size: 2rem;
}

.swiper-pagination {
  position: relative !important;
  margin-top: 1rem;

  @include media-breakpoint-up(lg) {
    margin-top: 1.5rem;
  }
}

.swiper-slide {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.swiper-pagination-bullet {
  position: relative;

  //pagination bullet clicable area extention
  &::before {
    content: '';
    width: 100%;
    height: 1.5rem;
    display: block;
    position: absolute;
    left: 0;
    top: -0.5rem;
  }
}
.swiper-pagination-bullet-active {
  width: 1.5rem;
}

.swiper-vertical :is(.swiper-button-next, .swiper-button-prev) {
	left: 50%;
	transform: rotate(90deg);
	transform-origin: left center;
}
.swiper-vertical .swiper-button-prev {
	top: calc(var(--swiper-navigation-size) / 2);
}
.swiper-vertical .swiper-button-next {
	top: auto;
	bottom: calc(var(--swiper-navigation-size) / 2);
}